import React from 'react';
import { Image } from 'react-native'
import { LinkedIn, Email, GitHub } from '@mui/icons-material';

import '../styles/Home.css';

const Home: React.FC = () => {
    return (
        <div className='home'>
            <div className='about'>
                <Image
                    source={require('../img/headshot.jpg')}
                    style={{ width: 400, height: 400, borderRadius: 400 / 2 }}
                />
                <h2>T. Wesley Bailey</h2>
                <div className='prompt'>
                <h3>Software Engineer</h3>
            </div>
            <div className='icons'>
                <LinkedIn />
                <Email />
                <GitHub />
            </div>
            </div>
            
            {/* Your component content goes here */}
            <div className='skills'>
                <h1 className='skillHeader'>Skills</h1>
                <ol className='list'>
                    <li className='item'>
                        <h2>Backend</h2>
                        <span>
                            Java, Elasticsearch, MySQL, PostgreSQL, .NET, GraphQL
                        </span>
                    </li>
                    <li className='item'>
                        <h2>Frontend</h2>
                        <span>
                            ReactJS, Redux, HTML, CSS, NPM, MaterialUI
                        </span>
                    </li>
                    <li className='item'>
                        <h2>Languages</h2>
                        <span>
                            Java, JavaScript, TypeScript, Python, SQL, HTML, CSS, C, C#
                        </span>
                    </li>
                </ol>
            </div>
        </div>
        
    );
};

export default Home;