import React from 'react';
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import SchoolIcon from '@mui/icons-material/School';
import WorkIcon from '@mui/icons-material/Work';

const Experience: React.FC = () => {
    return (
        <div>
            <VerticalTimeline lineColor='#21325e'>
                <VerticalTimelineElement
                    className="vertical-timeline-element--work"
                    contentStyle={{ background: '#00a0db', color: '#fff' }}
                    contentArrowStyle={{ borderRight: '7px solid  #00a0db' }}
                    date="2022 - present"
                    iconStyle={{ background: '#00a0db', color: '#fff' }}
                    icon={<WorkIcon />}
                >
                    <h3 className="vertical-timeline-element-title">Work Experience</h3>
                    <h4 className="vertical-timeline-element-subtitle">Cvent Inc</h4>
                    <p>
                        Built and maintained frontend and backend applications in a React/Redux and Java environment, using Elasticsearch and Postgres databases.
                    </p>
                </VerticalTimelineElement>
                <VerticalTimelineElement
                    className="vertical-timeline-element--education"
                    contentStyle={{ background: 'rgb(233, 30, 99)', color: '#fff' }}
                    contentArrowStyle={{ borderRight: '7px solid  rgb(233, 30, 99)' }}
                    date="2018 - 2022"
                    iconStyle={{ background: 'rgb(233, 30, 99)', color: '#fff' }}
                    icon={<SchoolIcon />}
                >
                    <h3 className="vertical-timeline-element-title">Education</h3>
                    <h4 className="vertical-timeline-element-subtitle">Sewanee: The University of the South</h4>
                    <p>
                        Double major in Computer Science and Russian; GPA 3.72
                    </p>
                </VerticalTimelineElement>
                {/* Add more VerticalTimelineElement components for each timeline item */}
            </VerticalTimeline>
        </div>
    );
};

export default Experience;